import React, {Component, CSSProperties, ChangeEvent} from "react";
import {FormControl, InputLabel, Input, Select, MenuItem, Card, CardContent} from "@material-ui/core";
import _ from 'lodash';

export class Form extends Component {
    displayName = Form.name;

    styles = {
        card: {
            display: 'flex',
            flex: '1',
            flexDirection: 'row',
            justifyContent: 'center',
            width: '100%'
        } as CSSProperties,
        form: {
            maxWidth: '640px',
            width: '100%'
        } as CSSProperties,
        cardContent: {
            flex: '1',
            flexDirection: 'column',
            display: 'flex'
        } as CSSProperties
    };

    constructor(props: any) {
        super(props);
        this.debounced = _.debounce(this.debounced.bind(this), 250);
    }

    state = {
        name: '',
        surname: '',
        secondName: '',
        selectedCity: null,
        cities: [],
        districts: [],
        selectedDistrict: null
    };

    componentDidMount(): void {
        this.getDistricts();
    }

    cities = [{Id: 1, Name: 'Тула'}, {Id: 2, Name: 'Щекино'}, {Id: 3, Name: 'Киреевск'}, {Id: 4, Name: 'Новомосковск'}, {Id: 5, Name: 'Богородицк'}];

    debounced = (value: any) => {
        console.log(value);
    };

    nameChange = (event: ChangeEvent<HTMLInputElement>) => {
        this.setState({
            name: event.target.value
        }, () => {this.debounced(this.state.name)});     
    };
    surnameChange = (event: React.ChangeEvent<HTMLInputElement>) => {
        this.setState({
            surname: event.target.value
        }, () => {this.debounced(this.state.surname)});     
    };
    secondNameChange = (event: React.ChangeEvent<HTMLInputElement>) => {
        this.setState({
            secondName: event.target.value
        }, () => {this.debounced(this.state.secondName)});     
    };
    cityChange = (event: any) => {
        this.setState({
            selectedCity: event.target.value
        }, () => {this.debounced(this.state.selectedCity)});
    }

    districtChange = (event: any) => {
        this.setState({
            selectedDistrict: event.target.value
        }, () => {
            this.getCities();
            this.debounced(this.state.selectedDistrict)
        });
    }

    getCities = () => {
        fetch(`api/city?districtId=${this.state.selectedCity}`)
            .then(res => res.json())
            .then(res => {
                this.setState({
                    cities: []
                });
                res.forEach( (s: any) => this.setState({
                    cities: [...this.state.cities, {Id: s.Id, Name: s.Name}]
                }));
            })
            .catch(error => console.log(error));
    };

    getDistricts = () => {
        fetch(`api/district`)
            .then(res => {
                debugger;
                this.setState({
                    districts: []
                });
                // this.setState({
                //     districts: [...res]
                // });
            })
            .catch(error => console.log(error));
    };

    render() {
        return (
            <div style={this.styles.card}>
                <Card style={this.styles.form}>
                    <CardContent style={this.styles.cardContent}>
                        <FormControl>
                            <InputLabel htmlFor="name">Имя</InputLabel>
                            <Input value={this.state.name} id="name" onChange={this.nameChange} />
                        </FormControl>
                        <FormControl>
                            <InputLabel htmlFor="surname">Фамилия</InputLabel>
                            <Input value={this.state.surname} id="surname" onChange={this.surnameChange} />
                        </FormControl>
                        <FormControl>
                            <InputLabel htmlFor="secondName">Отчество</InputLabel>
                            <Input value={this.state.secondName} id="secondName" onChange={this.secondNameChange} />
                        </FormControl>
                        <FormControl>
                            <InputLabel id="city">Район</InputLabel>
                            <Select value={this.state.selectedDistrict} onChange={this.cityChange}>
                                <MenuItem value={0}>Не выбран</MenuItem>
                                {!!this.state.districts.length && this.state.districts.map((district: any, k) => {
                                return (<MenuItem key={k} value={district.Id}>{district.Name}</MenuItem>)
                                })}
                            </Select>
                        </FormControl>
                    </CardContent>
                </Card>
            </div>
            
        );
    }
}
